import {ProfileAPI} from "../API/API";

const ADD_POST = "socNet/profile/ADD-POST";
const CHANGE_POST_TEXT = "socNet/profile/CHANGE-POST-TEXT";
const SET_USER_PROFILE = "socNet/profile/SET-USER-PROFILE";
const TOGGLE_IS_FETCHING = "socNet/profile/TOGGLE-IS-FETCHING";
const SET_STATUS = "socNet/profile/SET-STATUS";

export const setStatus = (status) => ({type: SET_STATUS, status: status});
export const addPost = (message) => ({type: ADD_POST, message: message});
export const changeText = (text) => ({type: CHANGE_POST_TEXT, text: text});
export const setUserProfile = (data) => ({type: SET_USER_PROFILE, data});
export const toggleFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});

let initialState = {
    status: '',
    isFetching: false,
    userId :4986,
    id: 4986, name: 'ADMIN!!', avatar: '', PostText: '', posts: [
        {id: 49861, name: 'sdsadas', message: "Hello People"},
        {id: 49862, name: 'rewe', message: "Have a nice day!!"},
        {id: 49863, name: 'sdsadas', message: "Everything is good!!"},
    ]
};

export const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST: {
            return {
                ...state,
                posts: [{id: 49864, name: 'Vaso', message: action.message,}, ...state.posts],
            };

        }
        case SET_USER_PROFILE: {
            return {
                ...state,
                id: action.data.userId,
                avatar: action.data.photos.small,
                name: action.data.fullName,
                contacts: {...action.data.contacts},
                photos:{...action.data.photos},
            }
        }
        case TOGGLE_IS_FETCHING: {
            return {
                ...state,
                isFetching: action.isFetching
            }
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        default: {
            return state
        }

    }
};

export const setProfile = (userId) => {
    return ((dispatch) => {
            dispatch(toggleFetching(true));
            ProfileAPI.setProfile(userId).then(data => {
                dispatch(setUserProfile(data));
                dispatch(toggleFetching(false));
            });
        }

    )
};
export const setUserStatus = (id) => {
    return( (dispatch) => {
            ProfileAPI.getStatus(id).then(data => {
                dispatch(setStatus(data)
                )})
    })
};


export const updateUserStatus = (status) => {
    return ((dispatch) => {
        ProfileAPI.updateStatus(status)
            .then(response => {
            if (!response.data.resultCode) {
                dispatch(setStatus(status))
            }
        });
    })
};
